.footer {
  padding-top: 60px;
  padding-bottom: 60px;
  background-color: var(--light-color);

  &__block {
    @include small-desktop {
      flex-direction: column;
      align-items: center;
    }

    display: flex;
    justify-content: space-between;
    margin-bottom: 45px;
    padding-bottom: 35px;
    border-bottom: 2px solid var(--gold-color);

    &-title {
      display: flex;
      flex-direction: column;
      align-items: center;
    }

    &-copy {
      display: flex;
      flex-direction: column;
      align-items: center;
      gap: 35px;
    }
  }

  &__info {
    @include small-desktop {
      margin-bottom: 25px;
    }

    &-list {
      @include small-desktop {
        gap: 50px;
      }

      @include mobile {
        flex-wrap: wrap;
        justify-content: center;
      }

      display: flex;
      gap: 75px;
    }

    &-item {
      @include small-desktop {
        max-width: 200px;
      }

      @include small-tablet {
        max-width: 125px;
      }

      display: flex;
      flex-direction: column;
      align-items: center;
    }

    &-title {
      @include small-tablet {
        font-size: 16px;
      }

      margin: 25px 0;
      font-family: var(--second-family);
      font-weight: 400;
      font-size: 20px;
      letter-spacing: 0.05em;
      color: #000;
    }

    &-address {
      @include small-tablet {
        font-size: 14px;
      }

      font-size: 17px;
      text-align: center;
      color: #000;
    }

    &-phone {
      font-size: 17px;
      text-align: center;
      color: #000;
      transition: color .5s ease-in-out;

      &:hover {
        color: var(--gold-color);
      }
    }

    &-clock {
      @include small-tablet {
        font-size: 14px;
      }

      margin: 0;
      font-size: 17px;
      text-align: center;
      color: #000;
    }
  }

  &__socials {
    display: flex;
    align-items: center;

    &-list {
      display: flex;
      align-items: center;
      gap: 50px;
    }
  }
}

.icon {
  width: 25px;
  height: 25px;
  fill: var(--gold-color);
}

.copy {
  @include mobile {
    font-size: 12px;
  }

  display: flex;
  justify-content: center;
  margin: 0;
  text-align: center;
  font-size: 17px;
  color: #000;
}
