.header {
  position: absolute;
  width: 100%;
  margin-bottom: 30px;
  padding-top: 40px;
  z-index: 25;

  &__block {
    display: flex;
    justify-content: space-between;
    align-items: center;
  }

  &__recording {
    @include mobile {
      padding: 10px 10px;
      font-size: 10px;
    }

    display: flex;
    justify-content: center;
    align-items: center;
    padding: 15px 20px;
    border: 2px solid var(--gold-color);
    font-weight: 900;
    font-size: 14px;
    text-align: center;
    color: #000;
    transition: color .5s ease-in-out, background .5s ease-in-out;

    &:focus {
      color: var(--light-color);
      background-color: var(--gold-color);
    }

    &:hover {
      color: var(--light-color);
      background-color: var(--gold-color);
    }
  }
}

.nav {
  @include tablet {
    display: none;
  }

  &__list {
    display: flex;
    align-items: center;
  }

  &__item {
    &:not(:last-child) {
      margin-right: 50px;
    }
  }

  &__link {
    font-weight: 400;
    font-size: 18px;
    color: #979797;
    outline: none;
    transition: color .5s ease-in-out;

    &:focus {
      color: #000;
    }

    &:hover {
      color: #000;
    }
  }

  &__active {
    color: #000;
  }
}

.burger {
  position: relative;
  display: flex;
  align-items: center;
  gap: 16px;
  padding-right: 7px;
  font-family: var(--second-family);
  font-weight: 700;
  font-size: 18px;
  line-height: 100%;
  text-transform: uppercase;
  color: var(--gold-color);

  &__line {
    position: relative;
    top: -4px;
    display: flex;
    width: 30px;
    height: 2px;
    background: var(--gold-color);
    transition: transform .5s ease-in-out;

    &::after {
      content: '';
      position: absolute;
      top: 7px;
      left: 0;
      width: 17px;
      height: 2px;
      background: var(--gold-color);
      transition: transform .5s ease-in-out;
    }

  }

  &.active .burger__line {
    transform: rotate(45deg);
    top: 0;

    &::after {
      transform: rotate(-90deg);
      top: 0;
      width: 30px;
    }
  }

  &__recording {
    @include mobile {
      margin-bottom: 0;
      padding: 10px 10px;
      font-size: 24px;
    }

    display: flex;
    justify-content: center;
    align-items: center;
    margin-bottom: 50px;
    padding: 15px 20px;
    max-width: 250px;
    border: 2px solid var(--gold-color);
    font-family: var(--second-family);
    font-size: 32px;
    line-height: 100%;
    color: var(--gold-color);
    transition: color 0.5s ease, background .5s ease;

    &:hover {
      color: #000;
      background-color: var(--gold-color);
    }
  }

  &__block {
    @include small-desktop {
      gap: 0;
    }

    @include mobile {
      flex-wrap: wrap;
      justify-content: center;
      top: 25px;
    }

    position: relative;
    top: 70px;
    display: flex;
    gap: 59px;

    &-left {
      @include big-desktop {
        height: 510px;
      }

      @include small-desktop {
        min-width: 500px;
      }

      @include tablet {
        min-width: 300px;
      }

      @include small-tablet {
        justify-content: normal;
        gap: 30px;
        min-width: 215px;
        height: 100%;
      }

      @include mobile {
        align-items: center;
        gap: 15px;
      }

      display: flex;
      flex-direction: column;
      justify-content: space-between;
      min-width: 606px;
      height: 800px;
    }

    &-right {
      display: flex;
      flex-direction: column;
      align-items: center;
    }

    &-phone {
      margin-bottom: 25px;
    }
  }

  &__text {
    transition: opacity .5s ease-in-out;
  }

  &__menu {
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background: #0c0c0c;
    transform: translateX(-100%); /* Скрываем за экраном */
    transition: transform .3s ease, display .3s ease;
    opacity: 0;
  }

  &__link {
    @include small-tablet {
      font-size: 24px;
    }

    font-family: var(--second-family);
    font-size: 32px;
    line-height: 100%;
    color: #979797;
    transition: color .5s ease;

    &-drop {
      margin-bottom: 16px;
    }
  }

  &__link:hover {
    color: var(--light-color);
  }

  &__list {
    @include small-tablet {
      margin: 0;
    }

    display: flex;
    flex-direction: column;
    gap: 32px;
    margin-bottom: 100px;
    max-width: 616px;
  }

  &__copy {
    @include small-tablet {
      font-size: 12px;
    }

    @include mobile {
      display: none;
    }

    font-weight: 400;
    font-size: 18px;
    color: #979797;
  }

  &__info {
    @include small-tablet {
      font-size: 24px;
    }

    @include mobile {
      margin-bottom: 20px;
    }

    margin: 0;
    margin-bottom: 25px;
    font-weight: 400;
    font-size: 32px;
    color: #979797;
  }

  &__address {
    @include small-tablet {
      font-size: 16px;
    }

    @include mobile {
      text-align: center;
      font-size: 14px;
    }

    margin: 0;
    margin-bottom: 25px;
    font-weight: 400;
    font-size: 18px;
    color: #979797;
  }

  &__phone {
    font-weight: 400;
    font-size: 18px;
    color: #979797;
    transition: color .5s ease-in-out;

    &:hover {
      color: var(--gold-color);
    }
  }
}

.burger__socials-list {
  @include small-tablet {
    gap: 30px;
  }

  display: flex;
  gap: 50px;
}

/* Меню открывается */
.burger.active + .burger__menu {
  opacity: 1;
  z-index: 55;
  transform: translateX(0);
}

.burger__menu-close--icon {
  display: none; /* Скрываем, если не открыто */
}

.burger__menu.active .burger__menu-close--icon {
  @include mobile {
    top: -10px;
    right: -40px;
  }

  position: absolute;
  top: 0;
  right: 0;
  display: block; /* Показываем, если меню открыто */
}

/* Можно добавить стили для крестика, если хотите */
.burger__menu-close--icon::before {
  content: '✖'; /* Символ крестика */
  font-size: 24px; /* Размер крестика */
  color: var(--gold-color);
  cursor: pointer;
}

.old {
  &__nav {
    &-active {
      color: var(--light-color);
    }
  }

  &__recording {
    color: var(--light-color);

    &:hover {
      color: #000;
    }
  }
}
