.fixed-record-btn {
  @include mobile {
    z-index: 5;
  }

  position: fixed;
  bottom: 80px;
  right: 20px;
  z-index: 1000;
  opacity: 0;
  visibility: hidden;
  transition: opacity 0.3s, visibility 0.3s;
}

.fixed-record-btn.visible {
  opacity: 1;
  visibility: visible;
}

.fixed-record-btn a {
  @include mobile {
    padding: 15px 15px;
    font-size: 12px;
  }

  position: relative;
  display: inline-block;
  padding: 15px 25px;
  background-color: var(--gold-color);
  color: #fff;
  border-radius: 50px;
  font-weight: bold;
  text-decoration: none;
  transition: transform 0.3s;
  box-shadow: 0 4px 10px rgba(0, 0, 0, 0.3);
}

.fixed-record-btn a:hover {
  transform: scale(1.1);
}

.fixed-record-btn a::before {
  content: "";
  position: absolute;
  top: 0;
  left: -20%;
  transform: translate(-50%, -50%);
  width: 200px;
  height: 100%;
  background-color: var(--gold-color);
  border-radius: 60%;
  animation: pulse 1.5s infinite;
}

@keyframes pulse {
  0% {
    transform: scale(1);
    opacity: 0.6;
  }
  50% {
    transform: scale(1.3);
    opacity: 0;
  }
  100% {
    transform: scale(1);
    opacity: 0;
  }
}
