.about-page {
  padding: 30px 0;
  background-color: var(--light-color);

  &__title {
    color: #000;
  }

  &__block {
    display: flex;
    flex-wrap: wrap;
    gap: 24px;
  }

  &__card {
    display: flex;
    flex-direction: column;
    max-width: 410px;

    &-info {
      display: flex;
      flex-direction: column;
      align-items: center;
      padding: 30px 0;
      background-color: #212121;
    }

    &-name {
      margin: 0;
      margin-bottom: 25px;
      font-family: var(--second-family);
      font-weight: 400;
      font-size: 22px;
      letter-spacing: 0.05em;
      text-align: center;
      color: var(--light-color);
    }

    &-list {
      display: flex;
      align-items: center;
      gap: 20px;
    }

    &-icon {
      width: 16px;
      height: 15px;
      fill: var(--gold-color);
    }
  }
}

.card {
  &__img {
    width: 410px;
    height: 340px;
  }
}
