.services {
  @include small-desktop {
    padding-bottom: 70px;
  }

  padding-bottom: 100px;

  &__page {
    @include small-tablet {
      padding: 0;
    }

    padding-bottom: 30px;
  }

  &__block {
    @include small-desktop {
      justify-content: center;
    }

    display: flex;
    justify-content: space-between;

    &-title {
      @include small-desktop {
        margin-bottom: 70px;
      }

      @include tablet {
        margin-bottom: 50px;
      }

      display: flex;
      justify-content: center;
      margin-bottom: 100px;
    }
  }

  &__content {
    max-width: 724px;
  }

  &__list {
    display: flex;
    flex-wrap: wrap;
    gap: 50px;
    margin-bottom: 60px;
  }

  &__item {
    @include tablet {
      max-width: 100%;
    }

    @include small-tablet {
      justify-content: normal;
      padding: 30px;
      min-height: 280px;
    }

    @include mobile {
      padding: 55px;
      width: 100%;
      min-height: 180px;
      text-align: center;
    }

    display: flex;
    flex-direction: column;
    justify-content: space-between;
    align-items: center;
    border: 2px solid #e9c664;
    padding: 50px;
    max-width: 337px;
    min-height: 337px;
    gap: 25px;

    img {
      @include mobile {
        width: 30px;
      }

      width: 45px;
    }
  }

  &__name {
    margin: 0;
    font-family: var(--second-family);
    font-weight: 400;
    font-size: 10px;
    letter-spacing: 0.05em;
    color: var(--light-color);
  }

  &__descr {
    @include mobile {
      display: none;
    }

    margin: 0;
    font-size: 10px;
    text-align: center;
    line-height: 15px;
    color: var(--light-color);
  }

  &__link {
    display: flex;
    justify-content: center;
    align-items: center;
    border: 2px solid #e9c664;
    padding: 15px 20px;
    font-weight: 600;
    font-size: 20px;
    color: #e9c664;
    transition: color .5s ease-in-out, background .5s ease-in-out;

    &:focus {
      color: #000;
      background-color: var(--gold-color);
    }

    &:hover {
      color: #000;
      background-color: var(--gold-color);
    }
  }

  &__video {
    width: 87%;
    height: auto;
    display: block;
    object-fit: cover;

    &-block {
      @include big-desktop {
        width: 50%;
      }

      @include small-desktop {
        display: none;
      }

      position: relative;
      display: flex;
      justify-content: right;
      width: 87%;
      overflow: hidden;
    }
  }

  &__none {
    display: none;
  }

  &__page {
    &-content {
      max-width: 100%;
    }

    &-list {
      @include small-tablet {
        display: none;
      }

      gap: 24px;
      justify-content: center;
    }
  }

  &__nav {
    &-link {
      &:hover {
        color: var(--light-color);
      }
    }
  }
}
