.contacts-page {
  padding: 30px 0;

  &__block {
    display: flex;
    flex-wrap: wrap;
    justify-content: space-between;
    gap: 24px;
  }

  &__info {
    @include small-desktop {
      align-items: center;
      width: 100%;
    }

    display: flex;
    flex-direction: column;
    align-items: flex-start;
  }

  &__title {
    @include big-desktop {
      font-size: 28px;
    }

    @include mobile {
      font-size: 16px;
    }

    margin: 0;
    margin-bottom: 22px;
    max-width: 450px;
    font-family: var(--second-family);
    font-weight: 400;
    font-size: 50px;
    letter-spacing: 0.05em;
    color: var(--light-color);
  }

  &__subtitle {
    @include mobile {
      font-size: 14px;
    }

    margin: 0;
    margin-bottom: 18px;
    font-family: var(--second-family);
    font-weight: 400;
    font-size: 22px;
    letter-spacing: 0.05em;
    text-align: center;
    color: var(--light-color);
  }

  &__address {
    @include mobile {
      font-size: 14px;
    }

    margin-bottom: 22px;
    font-size: 18px;
    text-align: center;
    color: var(--light-color);
  }

  &__phone {
    margin-bottom: 22px;
    font-size: 18px;
    text-align: center;
    color: #fff;
  }

  &__map {
    @include small-desktop {
      width: 100%;
    }

    width: 636px;
  }
}
