.hero {
  position: relative;

  @include small-desktop {
    margin-bottom: 50px;
  }

  margin-bottom: 71px;
  background-color: #f3f3f3;

  &__img {
    @include big-desktop {
      height: 500px;
    }

    @include small-desktop {
      height: 400px;
    }

    @include tablet {
      height: 300px;
    }

    @include mobile {
      height: 200px;
    }

    height: 730px;
  }

  &__services {
    @include small-desktop {
      margin: 0;
    }

    @include tablet {
      height: 475px;
    }

    @include small-tablet {
      height: 380px;
    }

    @include mobile {
      height: 250px;
    }

    width: 100%;
    height: 610px;
    background-image: url(../img/logo-mad.jpg);
    background-position: top;
    background-size: cover;
    background-repeat: no-repeat;
  }

  &__about {
    margin: 0;
  }
}
