.price {
  @include mobile {
    padding: 30px 0;
  }

  padding: 60px 0;
  background: #212121;

  &__title {
    &-name {
      margin-bottom: 30px;
    }
  }

  &__list {
    @include small-desktop {
      justify-content: center;
    }

    display: flex;
    flex-wrap: wrap;
    justify-content: space-between;
    gap: 50px;

    &-top {
      margin-bottom: 50px;
    }

    &-bottom {
      margin-bottom: 50px;
    }
  }

  &__item {
    @include small-tablet {
      justify-content: center;
    }

    @include mobile {
      justify-content: space-between;
      gap: 0;
      min-width: 100%;
    }

    display: flex;
    align-items: center;
    gap: 10px;
    min-width: 419px;

    div {
      @include small-tablet {
        width: 125px;
      }

      @include mobile {
        display: none;
      }

      width: 259px;
      border: 1px solid var(--gold-color);
    }

    &-rofl {
      @include mobile {
        justify-content: space-between;
      }

      justify-content: center;
    }
  }

  &__name {
    @include big-desktop {
      font-size: 20px;
    }

    @include small-tablet {
      font-size: 16px;
    }

    @include mobile {
      font-size: 14px;
    }

    margin: 0;
    font-family: var(--second-family);
    font-weight: 400;
    font-size: 24px;
    letter-spacing: 0.05em;
    color: var(--light-color);
  }

  &__value {
    @include big-desktop {
      font-size: 20px;
    }

    @include small-tablet {
      font-size: 16px;
    }

    @include mobile {
      font-size: 12px;
    }

    margin: 0;
    font-family: var(--second-family);
    font-weight: 400;
    font-size: 24px;
    letter-spacing: 0.05em;
    color: var(--light-color);
  }
}
